import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CartService } from '@services/modules/cart/services/cart.service';
import { CartTransfers } from '@schemas/modules/cart/cart-transfers';
import { ICart } from '@schemas/modules/cart/cart';

@Component({
    selector: 'tz-mob-book-block',
    templateUrl: './mob-book-block.component.html',
    styleUrls: ['./mob-book-block.component.scss'],
})
export class MobBookBlockComponent implements OnInit {
    @Input() totalPrice!: number;
    @Input() priceCurrency!: string;
    @Input() cartData!: ICart;
    @Output() gotoCheckout: EventEmitter<void> = new EventEmitter<void>();

    public personsStr = '';
    public transfers: CartTransfers[] = [];

    constructor(private cartService: CartService) {}

    ngOnInit() {
        this.getTransfers();

        this.getPersons();
    }

    public onGotoCheckout(): void {
        this.gotoCheckout.emit();
    }

    private getTransfers(): void {
        this.transfers = this.cartService.getTransfers(this.cartData);
    }

    private getPersons(): void {
        this.personsStr = this.cartService.getCartPersonsStr(this.cartData);
    }
}
