import { Component, Injector, OnDestroy, OnInit } from '@angular/core';
import { ICart } from '@schemas/modules/cart/cart';
import { BaseServiceComponent } from '../components/base-service-component/base-service.component';
import { IFlightsCartData } from '@schemas/modules/flights/flights-cart-data';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { ServiceId } from '@core/constants/service-id.const';
import { ListenerId } from '@core/constants/listener-id.const';
import { StateFrom } from '@core/constants/state-from.const';
import { TrainUtilsService } from '@services/modules/transport/trains/train.utils';
// eslint-disable-next-line max-len
import { TripTimelineDialogComponent } from '../../trip-timeline/components/trip-timeline-dialog/trip-timeline-dialog.component';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';

@Component({
    selector: 'tz-results-summary',
    templateUrl: './results-summary.component.html',
    styleUrls: ['./results-summary.component.scss'],
})
export class ResultsSummaryComponent extends BaseServiceComponent implements OnInit, OnDestroy {
    public cartData!: ICart;
    public timelineData: any[] = [];
    public transferCartData: ICart | undefined;
    public flightsCartData: IFlightsCartData | undefined;
    public accommodationCartData: IAccommodationsCartData | undefined;
    public carRentalCartData: ICart | undefined;
    public trainCartData: ICart | undefined;
    public servTypes = ServiceId;
    public combinationId = '';
    public mouseHoverId = '';
    public mobSummaryTimeline: 'summary' | 'timeline' = 'summary';
    public numberOfPassengers: number | undefined;

    private repriceListenerId = -1;

    constructor(injector: Injector, private trainUtilsService: TrainUtilsService) {
        super(injector);
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
    }

    public static onSubmitBooking(): void {
        console.error('submit booking');
    }

    ngOnInit(): void {
        this.initializeComponent();
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
        this.listenersService.unregisterListener(this.repriceListenerId);
    }

    public onRemoveService(serviceId: ServiceId, serviceCartData: ICart, optionId?: string | undefined): void {
        const self = this;

        this.removeService(serviceId, optionId, true).then(
            (isRemoved: boolean) => {
                if (isRemoved) {
                    self.loadCartData();
                }
            },
            (error) => {
                console.error(error);
            }
        );
    }

    public onChangeTicket(serviceId: ServiceId, cartItem?: any, data?: any) {
        this.cartRouterService.changeState(serviceId, cartItem, undefined, StateFrom.SUMMARY, {
            changeTickets: true,
            data,
        });
    }

    public onChangeOptions(serviceId: ServiceId, cartItem?: any) {
        this.cartRouterService.changeState(serviceId, cartItem, undefined, StateFrom.SUMMARY, {
            changeOptions: true,
            preserveSelection: true,
        });
    }

    public onModifyService(serviceId: ServiceId, id?: string) {
        this.cartRouterService.changeState(serviceId, id, undefined, StateFrom.SUMMARY);
    }

    /*public addItemToCart(flight: any) {
        this.flightsService
            .addFlightToCart(flight)
            .pipe(takeUntil(this.destroy$))
            .subscribe(
                (response: ICart) => {
                    this.userSearchService.searchCheckDep(ServiceId.FLIGHT, true, false, false, true);
                },
                (error) => {
                    console.log(error);
                }
            );
    }*/

    public getTrainTitle(): string {
        return this.trainUtilsService.getSelectedTitle(this.trainCartData);
    }

    public setHoverId(itemId: string): void {
        this.mouseHoverId = itemId;
    }

    public opneBasketTimeline(): void {
        if (this.cartData.items.length === 0) {
            return;
        }

        this.dialogService.openBasketTimelineDialog(TripTimelineDialogComponent, this.cartData.items);
    }

    public goToCheckout(): void {
        this.router.navigate(['/checkout']);
    }

    public mobTabChange(tab: 'summary' | 'timeline'): void {
        this.mobSummaryTimeline = tab;
    }

    private loadCartData() {
        console.log('getCartData()', this.cartService.getCartData());
        this.cartData = this.cartService.getCartData();
        this.combinationId = this.cartData.combId;
        this.flightsCartData = this.cartService.getCartData(ServiceId.FLIGHT);
        this.accommodationCartData = this.cartService.getCartData(ServiceId.ACC);
        this.transferCartData = this.cartService.getCartData(ServiceId.TRANSFER);
        this.carRentalCartData = this.cartService.getCartData(ServiceId.CAR_RENTAL);
        this.trainCartData = this.cartService.getCartData(ServiceId.TRAIN);

        this.timelineData = CartUtils.generateTimelineData(this.cartData.items, true, this.translateService);
        if (ServiceId) {
            const travellersData = CartUtils.getTravellersData(this.cartService.getCartData().items);
            this.numberOfPassengers = travellersData?.total;
        }
        /* if (this.flightsCartData) {
            this.flightsService.getPackages(this.flightsCartData.items[0].selectedItem.data).subscribe(
                (packages: Flight[]) => {
                    this.flightPackages = packages;
                },
                (error: any) => {}
            );
        }*/
    }

    private initializeComponent() {
        this.cartService.reInit().then(() => {
            this.loadCartData();
        });

        this.repriceListenerId = this.listenersService.registerListener(
            ListenerId.LISTEN_REPRICED,
            this.loadCartData,
            this
        );
    }
}
