import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatButtonToggleChange } from '@angular/material/button-toggle';

@Component({
    selector: 'tz-summary-timeline-tabs',
    templateUrl: './summary-timeline-tabs.component.html',
    styleUrls: ['./summary-timeline-tabs.component.scss'],
})
export class SummaryTimelineTabsComponent implements OnInit {
    @Input() selectedTab: 'summary' | 'timeline' = 'summary';
    @Output() tabChange: EventEmitter<'summary' | 'timeline'> = new EventEmitter<'summary' | 'timeline'>();

    constructor() {}

    ngOnInit() {}

    public onButtonToggle(option: MatButtonToggleChange): void {
        this.tabChange.emit(option.value);
    }
}
